<template>
  <div class="wrap">
    <div class="chess">
      <div>
        <span>第{{n}}手</span>
        <button @click="restart">重新开始</button>
      </div>
      <div class="row">
        <Cell @divClick="divClick(0,$event)" :cn="n" />
        <Cell @divClick="divClick(1,$event)" :cn="n" />
        <Cell @divClick="divClick(2,$event)" :cn="n" />
      </div>
      <div class="row">
        <Cell @divClick="divClick(3,$event)" :cn="n" />
        <Cell @divClick="divClick(4,$event)" :cn="n" />
        <Cell @divClick="divClick(5,$event)" :cn="n" />
      </div>
      <div class="row">
        <Cell @divClick="divClick(6,$event)" :cn="n" />
        <Cell @divClick="divClick(7,$event)" :cn="n" />
        <Cell @divClick="divClick(8,$event)" :cn="n" />
      </div>
      <div>{{result==null?'胜负未分':`胜方为${result}`}}</div>
    </div>
  </div>
</template>

<script>
import Cell from "./components/Cell";
export default {
  data() {
    return {
      n: 0,
      map: [
        [null, null, null],
        [null, null, null],
        [null, null, null]
      ],
      result: null
    };
  },
  components: {
    Cell
  },
  methods: {
    divClick(i, text) {
      console.log(text);
      this.n += 1;
      this.map[Math.floor(i / 3)][i % 3] = text;
      this.tell();
    },
    tell() {
      for (var i = 0; i < 3; i++) {
        if (
          this.map[i][0] !== null &&
          this.map[i][0] == this.map[i][1] &&
          this.map[i][1] == this.map[i][2]
        ) {
          this.result = this.map[i][0];
          return;
        }
      }
      for (var j = 0; j < 3; j++) {
        if (
          this.map[0][j] !== null &&
          this.map[0][j] == this.map[1][j] &&
          this.map[1][j] == this.map[2][j]
        ) {
          this.result = this.map[0][j];
          return;
        }
      }
      if (
        this.map[0][2] !== null &&
        this.map[0][2] == this.map[1][1] &&
        this.map[2][1] == this.map[2][0]
      ) {
        this.result = this.map[0][2];
        return;
      }
      if (
        this.map[0][0] !== null &&
        this.map[0][0] == this.map[1][1] &&
        this.map[1][1] == this.map[2][2]
      ) {
        this.result = this.map[0][0];
        return;
      }
    },
    restart() {
      for (var i = 0; i < this.$children.length; i++) {
        this.$children[i].clearText();
      }
      this.n = 0;
      this.result = null;
      return;
    }
  }
};
</script>

<style>
.row {
  display: flex;
}
.wrap {
  display: flex;
  justify-content: center;
  text-align: center;
}
</style>
