<template>
  <div>
    <div class="cell" @click="divClick">
      <template v-if="a">{{text}}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
export default {
  props: ["cn"],
  data() {
    return { a: false, text: " " };
  },
  methods: {
    divClick() {
      if (this.text !== " ") {
        return;
      }
      this.a = true;
      this.text = this.cn % 2 == 0 ? "x" : "o";
      this.$emit("divClick", this.text);
    },

    clearText() {
      this.text = " ";
    }
  }
};
</script>

<style>
.cell {
  display: flex;
  border: 1px solid black;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
}
</style>
